<template>
  <div class="container">
    <div class="page-popup-wrapper" style="position: absolute;width: 100%;z-index: 999">
      <mt-header class="header">
        <router-link to="/" slot="left">
          <mt-button class="header-left">
            <img slot="icon" src="../../static/logo_main_new.png" width="35" height="35" class="icon">
            <span class="header-left">99%的大温地产投资精英都在关注</span>
          </mt-button>
        </router-link>
        <span class="header-right" slot="right" v-on:click="popuWchat">免费发布房源</span>
      </mt-header>
    </div>
    <mt-popup v-model="wechat" popup-transition="popup-fade" class="mint-popup-wechat" :style="{ top: buttonBottom + 10 + 'px' }">
      <img src='../../static/logo_wechat.png' class="wechatimg" alt="公众号二维码"/>
      <span class="wetchat_text">长按识别二维码</span>
    </mt-popup>
    <div id="map" class="map" v-on:click="toggle">
    </div>
    <mt-button size="small" v-on:click="showfilter" class="mapfilter">筛选条件</mt-button>
    <div class="gallery" id="gallery">
      <mt-popup v-model="popupVisible" position="bottom" class="mint-popup" :modal=false>
        <img src="../../static/no_data.jpg" v-show="no_data_default" class="gallery_img"/>
        <mt-button size="small" id="btnfilter" v-on:click="showfilter" class="filter">筛选条件</mt-button>
        <transition name="bounce">
          <ul class="list-house"
              v-loading="loading"
              element-loading-text="加载中..."
              element-loading-spinner="el-icon-loading"
              id="list">
            <li class="item-houe" v-for="(house, hindex) in houses" v-bind:key="hindex" :class="{active: currentIndex == hindex}" id="listitem">
              <div class="selectstatus"></div>
              <a v-bind:href='house.url' style="text-decoration: none">
                <img v-bind:src='house.img' class="img-house" alt="房源图片"/>
                <div class="desc-house">
                  <p class="address">{{house.address}}</p>
                  <p class="configure">{{house.bedroom}}室{{house.toilet}}卫&nbsp;&nbsp;{{house.areas}}</p>
                  <p class="type">{{house.housetype}}</p>
                  <p class="price">{{house.price}}</p>
                </div>
              </a>
            </li>
            <li style="width: 50%"/>
          </ul>
        </transition>
      </mt-popup>
    </div>
   <div>
     <mt-popup v-model="filterPopupVisible" position="top" class="filter-popup" :modal="false">
       <div>
         <mt-header title="筛选条件">
           <router-link to="/" slot="right">
             <mt-button v-on:click="close">关闭</mt-button>
           </router-link>
         </mt-header>
         <div class="select">
           <span class="selectdec">选择大区：</span>
           <el-dropdown placement="bottom-start"  @command="handleGroup" trigger="click" class="selectgroup">
             <el-button type="default" class="selectdecin">
               {{groupname}}
             </el-button>
             <el-dropdown-menu slot="dropdown" class="selectgroup">
               <el-dropdown-item v-for="(group, gindex) in groups" v-bind:key="gindex" :command="gindex">{{group.groupname}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div class="select">
           <span class="selectdec">选择城市：</span>
           <el-dropdown placement="bottom-start" @command="handleCity" trigger="click" class="selectgroup" @visible-change="dropdownshow">
             <el-button type="default" class="selectdecin">
               {{cityname}}
             </el-button>
             <el-dropdown-menu slot="dropdown" class="selectgroup">
               <el-dropdown-item v-for="(city, cindex) in cities" v-bind:key="cindex" :command="cindex">{{city.cityname}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
         <div class="select">
           <span class="selectdec">房源类型：</span>
           <el-dropdown placement="bottom-start" @command="handleHouseType" trigger="click" class="selectgroup">
             <el-button type="default" class="selectdecin">
               {{housetypeValue}}
             </el-button>
             <el-dropdown-menu slot="dropdown" class="selectgroup">
               <el-dropdown-item v-for="(type, tindex) in types" v-bind:key="tindex" :command="tindex">{{type.name}}</el-dropdown-item>
             </el-dropdown-menu>
           </el-dropdown>
         </div>
       </div>
       <div class="select">
         <div>
           <span class="selectdec">开始时间：</span>
           <el-button type="default" class="selectgroup"
                      @click.native="openPicker('start')">
             {{STime}}
           </el-button>
         </div>
         <mt-datetime-picker
           ref="start"
           v-model="pickerStarttime"
           type="date"
           @confirm="(value) => changeStartTime(value)"
           year-format="{value} 年"
           month-format="{value} 月"
           date-format="{value} 日"
           :startDate='startDate'>
         </mt-datetime-picker>
       </div>
       <div class="select">
         <div>
           <span class="selectdec">结束时间：</span>
           <el-button type="default" class="selectgroup"
                      @click.native="openPicker('end')">
             {{ETime}}
           </el-button>
         </div>
         <mt-datetime-picker
           ref="end"
           v-model="pickerEndStarttime"
           type="date"
           @confirm="(value) => changeEndTime(value)"
           year-format="{value} 年"
           month-format="{value} 月"
           date-format="{value} 日"
           format="yyyy 年 MM 月 dd 日"
           :startDate='startDate'>
         </mt-datetime-picker>
       </div>
      <div class="toastParent">
         <span class="toastHint"  v-show="toastShow">
          {{toastHint}}
         </span>
      </div>
       <el-row class="btnconfirm" :gutter="20">
         <el-col :span="12">
           <mt-button type="primary" size="large" v-on:click="resetFilter">重置</mt-button>
         </el-col>
         <el-col :span="12">
           <mt-button type="primary" size="large" v-on:click="selectCompleted">确定</mt-button>
         </el-col>
       </el-row>
     </mt-popup>
   </div>
  </div>
</template>

<script type="text/babel">
/* eslint-disable no-undef */
import axios from 'axios'
// const address = {
//   '大温地区': [
//     {cityid: 'NewWestminster', cityname: '新西敏'},
//     {cityid: 'Vancouver', cityname: '温哥华'},
//     {cityid: 'Burnaby', cityname: '本拿比'},
//     {cityid: 'Richmond', cityname: '列治文'},
//     {cityid: 'WestVan', cityname: '西温哥华'},
//     {cityid: 'Coquitlam', cityname: '高贵林'},
//     {cityid: 'NorthVancouver', cityname: '北温'},
//     {cityid: 'Delta', cityname: '三角洲'},
//     {cityid: 'Langley', cityname: '兰里'},
//     {cityid: 'NorthSurrey', cityname: '北素里'},
//     {cityid: 'PortMoody', cityname: '满地宝'},
//     {cityid: 'MapleRidge', cityname: '枫树岭'},
//     {cityid: 'Bowen', cityname: '宝云岛'},
//     {cityid: 'PortCoquitlam', cityname: '高贵林港'},
//     {cityid: 'PittMeadows', cityname: '匹特草原'}],
//   '菲沙河谷': [
//     {cityid: 'WhiteRock', cityname: '白石'},
//     {cityid: 'Surrey', cityname: '素里'},
//     {cityid: 'Mission', cityname: '米逊'},
//     {cityid: 'Abbotsford', cityname: '阿伯茨福德'},
//     {cityid: 'Chilliwack', cityname: '奇利瓦克'},
//     {cityid: 'Hope', cityname: '霍普'},
//     {cityid: 'Harrison', cityname: '哈里森'},
//     {cityid: 'Kent', cityname: '肯特'},
//     {cityid: 'Yarrow', cityname: '亚罗'}],
//   '斯阔米什河谷': [
//     {cityid: 'Squimish', cityname: '斯阔米什'},
//     {cityid: 'Whistler', cityname: '惠斯勒'},
//     {cityid: 'Pemberton', cityname: '彭伯顿'}],
//   '阳光海岸地区': [
//     {cityid: 'SunshineCoast', cityname: '阳光海岸'}]
// }

export default {
  data () {
    return {
      popugroup: false,
      popupVisible: true,
      popupShowNecessery: true,
      filterPopupVisible: false,
      baiduurl: '',
      /* 所有要显示的房源 */
      houses: [],
      /* 国家 */
      countries: [],
      provs: [],
      groups: [],
      /* 城市 */
      cities: [],
      types: [{'name': '二手(MLS)房源', 'value': 'saling'}, {'name': '楼花房源', 'value': 'presale'}, {'name': '出租房源', 'value': 'rent'}],
      /* 选中国家 */
      country: 'Canada',
      /* 筛选省份 */
      prov: 'BC',
      /* 选中group */
      group: '',
      /* 选中城市 */
      city: '',
      bgndate: '',
      enddate: '',
      housetypeValue: '房源类型',
      housetype: '',
      // starttime: '公众开放日开始时间',
      endtime: '2018年05月18日',
      countryname: '选择国家',
      provname: '选择省份',
      groupname: '选择大区',
      cityname: '选择城市',
      map: {},
      marker: {},
      center: {lat: 49.2827291, lng: -123.1207375},
      zoom: 12,
      loading: true,
      markers: [],
      /* 点击事件有两种类型一种是地图的点击事件一种是marker的点击事件 */
      eventType: 'map',
      currentIndex: 0,
      lastIndex: 0,
      clickLastIndex: 0,
      buttonBottom: 0,
      wechat: false,
      wechatUrl: '../../static/logo_wechat.png',
      no_data_default: false,
      isClickMarker: false,
      pickerStarttime: new Date(),
      startDate: new Date('2018'),
      STime: '公众开放日开始时间',
      ETime: '公众开放日结束时间',
      pickerEndStarttime: new Date(),
      toastHint: '请先选择大区~',
      toastShow: false
    }
  },
  created () {
    axios.get('http://www.realtoraccess.com/web/get/groups/', {params: {'provid': this.prov}}).then((response) => {
      this.groups = response.data
    })
    axios.get('http://www.realtoraccess.com/web/get/cities/', {params: {'groupid': this.group}}).then((response) => {
      this.cities = response.data
      // console.log('city======>>>>' + response.data)
    })
  },
  mounted () {
    var mapDiv = document.getElementById('map')
    this.map = new google.maps.Map(mapDiv, {
      zoom: this.zoom,
      center: this.center
    })
    // console.log('mounted====>>>>>' + this.country + '----' + this.prov + '----' + this.group + '----' + this.city + '----' + this.housetype + '---bgndate' + this.bgndate + '----enddate=' + this.enddate)
    var url = 'http://www.realtoraccess.com/web/zone/listings/'
    axios.get(url, {params: {'country': this.country, 'prov': this.prov, 'group': this.group, 'city': this.city, 'bgndate': this.bgndate, 'enddate': this.enddate, 'housetype': this.housetype}}).then((response) => {
      this.houses = response.data
      if (this.houses == null || this.houses.length === 0) {
        this.no_data_default = true
      }
      response.data.forEach(this.generateMarker)
      this.loading = false
    })
    document.getElementById('list').addEventListener('scroll', this.handleScroll)
  },
  methods: {
    dropdownshow: function (ishow) {
      if (ishow) {
        if (this.group === '') {
          this.toastShow = !this.toastShow
          this.toastHint = '请先选择大区~'
          this.setCountDown()
        }
      }
    },
    setCountDown: function () {
      let outThis = this
      var timer = setInterval(function () {
        outThis.toastShow = !outThis.toastShow
        clearInterval(timer)
      }, 3000)
    },
    resetFilter: function () {
      this.groupname = '选择大区'
      this.cityname = '选择城市'
      this.housetypeValue = '房源类型'
      this.STime = '公众开放日开始时间'
      this.ETime = '公众开放日结束时间'
      this.group = ''
      this.city = ''
      this.housetype = ''
      this.bgndate = ''
      this.enddate = ''
    },
    formatDate: function (date) {
      const y = date.getFullYear()
      let m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      return y + '年' + m + '月' + d + '日'
    },
    dateAndtimeFormat: function (date, fmt) {
      var o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds(),
        'S': date.getMilliseconds()
      }
      if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
      for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
      return fmt
    },
    openPicker: function (picker) {
      this.$refs[picker].open()
    },
    popuWchat: function () {
      this.wechat = true
    },
    /* 根据经纬度添加标记 */
    generateMarker: function (house, index) {
      var lat = house.lat
      var lng = house.lng
      if (lat !== '' && lng !== '') {
        this.createdMarker(house.lat, house.lng, house.price, index)
        // console.log(index + '---lat: ' + lat + '<=>lng: ' + lng + '----' + house.address)
      }
    },
    /* 有经纬度的直接添加到地图上 */
    createdMarker: function (lat, lng, price, index) {
      let funGloble = this
      var marker = new google.maps.Marker({
        icon: 'http://maps.google.cn/mapfiles/ms/icons/red-dot.png',
        position: {lat: Number(lat), lng: Number(lng)},
        map: this.map,
        ent_type: 'marker'
      })
      if (index === 0) {
        this.currentIndex = index
        this.map.setCenter(marker.getPosition())
        marker.setIcon('http://maps.google.cn/mapfiles/ms/icons/blue-dot.png')
      }
      this.markers.push(marker)
      google.maps.event.addListener(marker, 'click', function (event) {
        funGloble.markerClick(index, marker, lat, lng)
      })
    },
    handleClick: function () {
      this.popupVisible = true
    },
    /* 切换房源列表是否显示 */
    toggle: function () {
      if (this.eventType === 'marker') {
        if (this.popupShowNecessery) {
          this.popupVisible = true
          this.popupShowNecessery = false
        }
        this.eventType = 'map'
      } else {
        if (this.popupVisible) {
          this.popupVisible = false
        } else {
          this.popupVisible = true
        }
      }
    },
    /* 显示筛选条件页面 */
    showfilter: function () {
      if (this.filterPopupVisible) {
        this.filterPopupVisible = false
      } else {
        this.filterPopupVisible = true
      }
      this.popupVisible = true
    },
    /* 筛选条件选择完成 */
    selectCompleted: function () {
      if (this.enddate < this.bgndate) {
        this.toastShow = !this.toastShow
        this.toastHint = '结束日期不能早于开始日期'
        this.setCountDown()
        return
      }
      // 关闭页面
      this.filterPopupVisible = false
      // this.opendate = this.startTime + '-' + this.endTime
      // console.log('opendate======>>>>' + this.opendate)
      this.getFilterHouse()
      this.loading = true
    },
    close: function () {
      this.filterPopupVisible = false
    },
    // handleCountry: function (index) {
    //   this.countryname = this.countries[index].countryname
    //   this.country = this.countries[index].countryid
    //   this.getprov(this.countries[index].countryid)
    // },
    // handleProv: function (index) {
    //   this.provname = this.provs[index].provname
    //   this.prov = this.provs[index].provid
    //   this.getgroup(this.provs[index].provid)
    // },
    handleGroup: function (gindex) {
      this.groupname = this.groups[gindex].groupname
      this.group = this.groups[gindex].groupid
      this.getcity(this.groups[gindex].groupid)
    },
    handleCity: function (cindex) {
      this.cityname = this.cities[cindex].cityname
      this.city = this.cities[cindex].cityid
    },
    handleHouseType: function (hindex) {
      this.housetypeValue = this.types[hindex].name
      this.housetype = this.types[hindex].value
    },
    getprov: function (country) {
      axios.get('http://www.realtoraccess.com/web/get/provs/', {params: { 'countryid': country }}).then((response) => {
        this.provs = response.data
      })
    },
    getgroup: function (provid) {
      axios.get('http://www.realtoraccess.com/web/get/groups/', {params: {'provid': provid}}).then((response) => {
        this.groups = response.data
        // console.log('group======>>>>' + response.data)
      })
    },
    getcity: function (groupid) {
      axios.get('http://www.realtoraccess.com/web/get/cities/', {params: {'groupid': groupid}}).then((response) => {
        this.cities = response.data
        // console.log('city======>>>>' + response.data)
      })
    },
    getFilterHouse: function () {
      // console.log(this.country + '----' + this.prov + '----' + this.group + '----' + this.city + '----' + this.housetype + '---bgndate' + this.bgndate + '----enddate=' + this.enddate)
      axios.get('http://www.realtoraccess.com/web/zone/listings/', {params: {'country': this.country, 'prov': this.prov, 'group': this.group, 'city': this.city, 'bgndate': this.bgndate, 'enddate': this.enddate, 'housetype': this.housetype}}).then((response) => {
        this.houses = response.data
        if (this.houses == null || this.houses.length === 0) {
          this.no_data_default = true
        } else {
          this.no_data_default = false
        }
        /* 清除地图上的原有标记 */
        for (var i = 0; i < this.markers.length; i++) {
          this.markers[i].setMap(null)
        }
        // console.log(this.houses)
        /* 清空标记集合 */
        this.markers = []
        /* 重新创建标记并添加到地图 */
        this.houses.forEach(this.generateMarker)
        this.loading = false
      }).catch(function (error) {
        console.log(error)
      })
    },
    changeStartTime: function (value) {
      this.bgndate = this.dateAndtimeFormat(new Date(value), 'yyyyMMddhhmmss')
      this.STime = this.formatDate(value)
      // console.log('startTime======>>>>' + new Date(value).getTime())
    },
    changeEndTime: function (value) {
      this.enddate = this.dateAndtimeFormat(new Date(value), 'yyyyMMddhhmmss')
      this.ETime = this.formatDate(value)
      if (this.enddate < this.bgndate) {
        this.toastShow = !this.toastShow
        this.toastHint = '结束日期不能早于开始日期'
        this.setCountDown()
      }
      // console.log('endTime======>>>>' + value)
    },
    /* 地图标记点击事件 */
    markerClick: function (index, marker, lat, lng) {
      this.eventType = 'marker'
      if (this.popupVisible) {
        this.popupShowNecessery = false
      } else {
        this.popupShowNecessery = true
      }
      // console.log('click-------lastIndex :' + this.lastIndex + 'currentIndex : ' + this.currentIndex + 'index : ' + index)
      this.handleList(index, lat, lng)
    },
    /* 处理选中地图之后的滚动事件 */
    handleList: function (index, lat, lng) {
      let outThis = this
      outThis.isClickMarker = true
      this.markers[this.lastIndex].setIcon('http://maps.google.cn/mapfiles/ms/icons/red-dot.png')
      this.map.setCenter({lat: Number(lat), lng: Number(lng)})
      /* 改变marker的颜色  并重新设置为地图中心 */
      this.markers[index].setIcon('http://maps.google.cn/mapfiles/ms/icons/blue-dot.png')
      this.lastIndex = index
      if (this.currentIndex === index) {
        return
      }
      this.currentIndex = index
      var itemWidth = document.getElementById('listitem').offsetWidth
      var totalDistance = (itemWidth + 12) * index
      var housrlist = document.getElementById('list')
      var step = 0
      var currentDistance = 0
      var timer = setInterval(function () {
        currentDistance = (itemWidth + 12) * step
        if (step >= index) {
          housrlist.scrollTo(totalDistance, 0)
          outThis.isClickMarker = false
          clearInterval(timer)
        } else {
          housrlist.scrollTo(currentDistance, 0)
        }
        step++
      }, 5)
    },
    handleScroll: function () {
      var leftDistance = document.getElementById('list').scrollLeft
      var itemWidth = document.getElementById('listitem').offsetWidth + 12
      var index = Math.ceil(leftDistance / itemWidth)
      /* 单个item滚动距离大于一半时才取当前index 选中当前列表 */
      if ((parseFloat(leftDistance) / parseFloat(itemWidth)).toFixed(2) > index - 0.05) {
        this.currentIndex = index
      }
      if (index >= this.houses.length) {
        this.currentIndex = this.houses.length - 1
      }
      var marker = this.markers[this.currentIndex]
      // 向左滑动 或者向右滑动
      if ((this.currentIndex !== index || this.lastIndex > this.currentIndex) && !this.isClickMarker) {
        /* 设置地图中心点 */
        this.map.setCenter(marker.getPosition())
        /* 改变最后一个marker的状态 */
        if (this.lastIndex !== -1) {
          this.markers[this.lastIndex].setIcon('http://maps.google.cn/mapfiles/ms/icons/red-dot.png')
          /* 改变当前marker的状态 */
          marker.setIcon('http://maps.google.cn/mapfiles/ms/icons/blue-dot.png')
        }
        this.lastIndex = this.currentIndex
      }
      // console.log('lastIndex : ' + this.lastIndex + '---->currentIndex :  ' + this.currentIndex + '-----—>index :  ' + index)
    }
  }
}
</script>
